@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body{
    
    font: 400 14px 'Poppins', sans-serif;
    background: #ffffff;
    -webkit-font-smoothing: antialiased;
}

input, button, textarea{
    font: 400 18px 'Poppins', sans-serif;
}

button{
    cursor: pointer;
}

form input{
    width: 100%;
    height: 60px;
    color:#333333;
    border: solid 1px #dcdce6;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea{
    width: 100%;
    min-height: 140px;
    color:#333333;
    border: solid 1px #dcdce6;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}

.button{
    width: 100%;
    height: 60px;
    background-color: #e02041;
    border: 0;
    border-radius: 8px;
    color:#fff;
    font-weight: 700;
    margin-top: 16px;
    display: inline-block;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2s;
    transition: filter 0.2s;
    transition: filter 0.2s, -webkit-filter 0.2s;
}
.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link{
    display:flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}
.back-link svg{
    margin-right: 8px;
}
.back-link:hover{
    opacity: 0.8;
}
.top-header{
    width: 100%;
    height: 500px;
    /* background-image: url(../../assets/bg-progcientifica.jpg);
    background-position: bottom center;
    background-repeat: no-repeat;
    background-size: cover; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 20px;
}
.top-header .img-container{
    width: 100%;
    height: 500px;
    position: absolute;
    z-index: -99;
}
.top-header .img-container img{
    height: 500px;
}
.top-header h1{
    text-transform: uppercase;
    color: #fff;
    font-weight: 700;
    font-size: 40px;
    margin-left: 50px;
    margin-top: 40px;
}
.top-header h1 span{
    color:red;
}

/* ----- EVENT INFOS ----- */
.event-container{
    margin-top: 20px;
}

.title {
    font-size: 14px;
    font-weight: 600;
    border-bottom: solid 2px #eee;
    margin-bottom: 5px;
}

.speaker-infos {
    display: inline-block;
    width: 100%;
    margin-bottom: 10px;
    margin-top: 10px;
    cursor:pointer;
}
.speaker-img {
    width: 40px;
    float: left;
    overflow: hidden;
    border-radius: 50%;
    margin-right: 10px;
    margin-bottom: 10px;
}
.speaker-img img {
    width: 100%;
}
.speaker-name{
    color: #0072bc;
    font-weight: bold;
    font-size: 14px;
    margin-top: 2px;
}
.category {
    font-size: 10px;
    color: #9698b6;
    font-weight: bold;
    line-height: 6px;
}

.activity-infos{
    margin-top: 10px;
    padding-top: 10px;
}
.activity-infos .location, .activity-infos .date{
    font-size: 12px;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    display: flex;
}
.activity-infos .date{
    margin-bottom: 15px;
}
.activity-infos .icon{
    margin-right: 5px;
}
.activity-infos .speaker-infos, .activity .talk-infos{
    background-color: #f7f7f8;
}
.activity-infos .talk-infos {
    padding-top: 10px;
}
.activity-infos .talk-infos .speaker-infos{
    background-color: transparent;
    cursor: pointer;
}


.type, .activity .date, .activity .talk-title, .activity .dot{
    display: none;
}
.talk .talk-infos {
    border-left: solid 1px #eee;
}
.talk-infos .dot {
    width: 15px;
    height: 15px;
    background-color: #d30015;
    border: solid 4px #fff;
    border-radius: 50%;
    position: absolute;
    left: -8px;
    bottom: 72px;
}
.talk-infos .date{
    font-size: 12px;
    font-weight: bold;
    color: #0e1783;
}
.talk-infos .talk-title {
    font-size: 14px;
    font-weight: bold;
    color: #0e1783;
    line-height: 15px;
}
.talk-infos .date, .talk-infos .talk-title{
    margin-top: 15px;
}

/* -- TABS STYLE -- */
.nav-tabs .nav-link{
    text-align: center;
    flex: 1 1;
    border: none;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background-color: #f7f7f8;
    color: #9698b6;
    font-weight: bold;
    font-size: 14px;
    border-right: solid 2px #fff;
}
.nav-tabs .nav-item.show .nav-link, .nav-tabs .nav-link.active, .nav-tabs .nav-link:focus{
    color: #fff;
    font-weight: bold;
    background-color: #0072bc;
    border-color: transparent
}
.nav-tabs .nav-link:hover{
    background-color: #f1f1f6;
}
.nav-tabs .nav-link.active:hover{
    background-color: #0072bc;
    cursor: auto;
    cursor: initial;
}

/* -- MODAL -- */
.modal-body .highlight{
    color: #ffffff;
    display: block;
    background-color: #d30015;
    margin-left: -16px;
    padding-left: 16px;
    font-weight: 800;
}
.modal-header .speaker-img {
    width: 100%;
    border-radius: 0;
}
.modal-header h3{
    color: #0e1783;
    font-size: 40px;
    font-weight: 800;
    text-transform: uppercase;
    line-height: 1.1em;
    letter-spacing: 9px;
}
.modal-header p{
    color: #9698b5;
    font-size: 16px;
}
.modal-body h3{
    color: #0072bc;
    font-size: 30px;
    font-weight: 800;
    padding-right: 15px;
    margin-bottom: 15px;
}
.modal-body .talk-infos{
    color: #d30015;
    font-weight: 700;
    font-size: 12px;
    margin-top: 7px;
    border-top: solid 1px #f5f5f5;
}
.modal-body .talk-infos .talk-title{
    text-transform: uppercase;
    margin-top: 0px;
}
.modal-body svg {
    margin-top: -2px;
    margin-right: 3px;
}
.modal-body p{
    color: #9698b5;
    margin-bottom: 5px;
}

